import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { TechnologyserviceService } from './technologyservice.service';

@Component({
  selector: 'app-technology',
  templateUrl: './technology.component.html',
  styleUrls: ['./technology.component.css']
})
export class TechnologyComponent implements OnInit {

  constructor(private router:Router, private apiService:TechnologyserviceService) { }
  news;

  ngOnInit() {
    this.getNews();
  }
  getNews(){
    this.apiService.getNews().subscribe(response => {this.news = response});
  }

}
