import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {MatButtonModule, MatCheckboxModule} from '@angular/material';
import {MatCardModule} from '@angular/material/card';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { HomeComponent } from './home/home.component';
import { BusinessComponent } from './business/business.component';
import { EntertainmentComponent } from './entertainment/entertainment.component';
import { ApiService } from './home/api.service';
//import { ApiService } from './business/api.service';
import { RouterModule, Routes } from '@angular/router';
import { SportsComponent } from './sports/sports.component';
import { HttpClientModule } from '@angular/common/http';
import { BusinessserviceService } from './business/businessservice.service';
import { EntertainmentserviceService } from './entertainment/entertainmentservice.service';
import { SportsserviceService } from './sports/sportsservice.service';
import { TechnologyComponent } from './technology/technology.component';

const ROUTES: Routes=[
  { path:'', component:HomeComponent},
  { path:'business', component:BusinessComponent},
  { path:'entertainment', component:EntertainmentComponent},
  { path:'sports', component:SportsComponent},
  { path:'technology', component:TechnologyComponent},
  { path:'home', component:HomeComponent}
]

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    FooterComponent,
    HomeComponent,
    BusinessComponent,
    EntertainmentComponent,
    SportsComponent,
    TechnologyComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    MatButtonModule, MatCheckboxModule,
    MatCardModule,
    RouterModule.forRoot(ROUTES),
    HttpClientModule
  ],
  providers: [ApiService,BusinessserviceService,EntertainmentserviceService,SportsserviceService],
  //providers: [ApiService],
  bootstrap: [AppComponent]
  
})
export class AppModule { }
