import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { EntertainmentserviceService } from './entertainmentservice.service';

@Component({
  selector: 'app-entertainment',
  templateUrl: './entertainment.component.html',
  styleUrls: ['./entertainment.component.css']
})
export class EntertainmentComponent implements OnInit {

  constructor(private router:Router,private apiService:EntertainmentserviceService) { }
  news;

  ngOnInit() {
    this.getNews();
  }
  getNews(){
    this.apiService.getNews().subscribe(response => {this.news = response});
  }
  


}
